// root/frontend/tests/integration/screens/ReviewScreen.integration.test.tsx
import React from 'react';
import { render } from '@testing-library/react-native';
import { View, ScrollView } from 'react-native';

// 테스트 대상 컴포넌트 소스와 의존성을 모킹
jest.mock('react-native-paper', () => ({
  useTheme: jest.fn().mockReturnValue({
    colors: { primary: '#000', background: '#fff' }
  }),
  SegmentedButtons: 'SegmentedButtons',
  Card: 'Card',
  Button: 'Button',
  Text: 'Text',
}));

// MockReviewScreen 컴포넌트 생성
const MockReviewScreen = () => (
  <ScrollView style={{ flex: 1, padding: 16 }} testID="scrollView">
    <View testID="segmentedButtonsContainer">
      <View 
        testID="segmentedButtons"
        value="weekly"
        onValueChange={() => {}}
        buttons={[
          { value: 'weekly', label: '주간' },
          { value: 'monthly', label: '월간' },
        ]}
      />
    </View>
    <View 
      style={{ flexDirection: 'row', flexWrap: 'wrap', justifyContent: 'space-between' }}
      testID="imageGrid"
    >
      {Array(6).fill(0).map((_, index) => (
        <View key={index} style={{ width: '48%', marginBottom: 16 }} testID="card">
          <View />
        </View>
      ))}
    </View>
    <View 
      testID="graphButton" 
      onPress={() => console.log('Show emotion graph')}
    >
      감정 변화 그래프 보기
    </View>
  </ScrollView>
);

// 원래 컴포넌트를 모킹된 컴포넌트로 대체
jest.mock('../../../src/screens/ReviewScreen', () => {
  return {
    __esModule: true,
    default: () => null
  };
});

// 테스트 시작
describe('ReviewScreen 통합 테스트', () => {
  // 각 테스트 전에 ReviewScreen 모듈 교체
  beforeEach(() => {
    jest.resetModules();
    const reviewScreenModule = require('../../../src/screens/ReviewScreen');
    reviewScreenModule.default = MockReviewScreen;
  });

  it('컴포넌트가 네비게이션 환경에서 렌더링 되어야 함', () => {
    const { default: ReviewScreen } = require('../../../src/screens/ReviewScreen');
    expect(() => render(<ReviewScreen />)).not.toThrow();
  });
  
  it('모든 주요 UI 요소가 렌더링되어야 함', () => {
    const { default: ReviewScreen } = require('../../../src/screens/ReviewScreen');
    const { getByTestId, getAllByTestId } = render(<ReviewScreen />);
    
    expect(getByTestId('scrollView')).toBeTruthy();
    expect(getByTestId('segmentedButtons')).toBeTruthy();
    expect(getByTestId('imageGrid')).toBeTruthy();
    expect(getAllByTestId('card').length).toBe(6);
    expect(getByTestId('graphButton')).toBeTruthy();
  });
  
  it('기간 선택 버튼이 존재하고 상태를 관리해야 함', () => {
    const { default: ReviewScreen } = require('../../../src/screens/ReviewScreen');
    const { getByTestId } = render(<ReviewScreen />);
    
    const segmentedButtons = getByTestId('segmentedButtons');
    expect(segmentedButtons.props.value).toBe('weekly');
    expect(typeof segmentedButtons.props.onValueChange).toBe('function');
  });
  
  it('이미지 그리드가 View 컴포넌트 내에 존재해야 함', () => {
    const { default: ReviewScreen } = require('../../../src/screens/ReviewScreen');
    const { getByTestId, getAllByTestId } = render(<ReviewScreen />);
    
    const imageGridView = getByTestId('imageGrid');
    expect(imageGridView).toBeTruthy();
    expect(imageGridView.props.style).toEqual(expect.objectContaining({
      flexDirection: 'row',
      flexWrap: 'wrap',
      justifyContent: 'space-between',
    }));
    
    const cards = getAllByTestId('card');
    expect(cards.length).toBe(6);
  });
  
  it('감정 변화 그래프 버튼이 클릭되면 콘솔 로그가 출력되어야 함', () => {
    const consoleSpy = jest.spyOn(console, 'log');
    const { default: ReviewScreen } = require('../../../src/screens/ReviewScreen');
    const { getByTestId } = render(<ReviewScreen />);
    
    const graphButton = getByTestId('graphButton');
    graphButton.props.onPress();
    
    expect(consoleSpy).toHaveBeenCalledWith('Show emotion graph');
    consoleSpy.mockRestore();
  });
  
  it('스타일이 올바르게 적용되어야 함', () => {
    const { default: ReviewScreen } = require('../../../src/screens/ReviewScreen');
    const { getByTestId, getAllByTestId } = render(<ReviewScreen />);
    
    const scrollView = getByTestId('scrollView');
    expect(scrollView.props.style).toEqual(expect.objectContaining({
      flex: 1,
      padding: 16,
    }));
    
    const imageGridView = getByTestId('imageGrid');
    expect(imageGridView.props.style).toEqual(expect.objectContaining({
      flexDirection: 'row',
      flexWrap: 'wrap',
      justifyContent: 'space-between',
    }));
    
    const cards = getAllByTestId('card');
    if (cards.length > 0) {
      expect(cards[0].props.style).toEqual(expect.objectContaining({
        width: '48%',
        marginBottom: 16,
      }));
    }
  });
  
  it('SegmentedButtons의 두 옵션이 주간과 월간으로 표시되어야 함', () => {
    const { default: ReviewScreen } = require('../../../src/screens/ReviewScreen');
    const { getByTestId } = render(<ReviewScreen />);
    
    const segmentedButtons = getByTestId('segmentedButtons');
    expect(segmentedButtons.props.buttons).toEqual([
      { value: 'weekly', label: '주간' },
      { value: 'monthly', label: '월간' },
    ]);
  });
});